//  i-view
// 
<template>
    <ul class="nop-pager" :style="styles">
        <!--   -->
        <span v-if="showTotal" class="nop-pager-detail">
            {{ allPages }}{{ total }}
        </span>
        <!--END   -->
        <!--  -->
        <!--  -->
        <li @click="prev" :class="prevClass">
            <a>
                <Icon type="xiangzuo1" size="12"></Icon>
            </a>
        </li>
        <!--END  -->
        <!--  -->
        <li :title="1" @click="changePage(1)" :class="firstPageClass">
            <a>1</a>
        </li>
        <!--END  -->
        <!--  -->
        <li v-if="currentPage - 3 > 1" @click="fastPrev" class="nop-pager-item nop-pager-fastprev">
            <a>
                <Icon type="more" size="12"></Icon>
            </a>
        </li>
        <!--END  -->
        <!-- -2 -->
        <li :title="currentPage - 2" v-if="currentPage - 2 > 1" @click="changePage(currentPage - 2)" class="nop-pager-item">
            <a>{{ currentPage - 2 }}</a>
        </li>
        <!--END -2 -->
        <!-- -1 -->
        <li :title="currentPage - 1" v-if="currentPage - 1 > 1" @click="changePage(currentPage - 1)" class="nop-pager-item">
            <a>{{ currentPage - 1 }}</a>
        </li>
        <!--END -1 -->
        <!--  -->
        <li :title="currentPage" v-if="currentPage != 1 && currentPage != allPages" class="nop-pager-item nop-pager-item-active">
            <a>{{ currentPage }}</a>
        </li>
        <!--END  -->
        <!-- +1 -->
        <li :title="currentPage + 1" v-if="currentPage + 1 < allPages" @click="changePage(currentPage + 1)" class="nop-pager-item">
            <a>{{ currentPage + 1 }}</a>
        </li>
        <!--END +1 -->
        <!-- +2 -->
        <li :title="currentPage + 2" v-if="currentPage + 2 < allPages" @click="changePage(currentPage + 2)" class="nop-pager-item">
            <a>{{ currentPage + 2 }}</a>
        </li>
        <!--END +2 -->
        <!--  -->
        <li v-if="currentPage + 3 < allPages" @click="fastNext" class="nop-pager-item nop-pager-fastnext">
            <a>
                <Icon type="more" size="12"></Icon>
            </a>
        </li>
        <!--END  -->
        <!--  -->
        <li :title="allPages" v-if="allPages > 1" :class="lastPageClass" @click="changePage(allPages)">
            <a>{{ allPages }}</a>
        </li>
        <!--END  -->
        <!--  -->
        <li :class="nextClass" @click="next">
            <a>
                <Icon type="xiangyou1" size="12"></Icon>
            </a>
        </li>
        <!--END  -->
        <!--END  -->
        <!-- pageSize-->
        <Select class="nop-pager-select" v-model="currentPageSize" @input="onSize">
            <Option v-for="size in pageSizeOpts" :key="size" :value="size">{{size}}/</Option>
        </Select>
        <!--END pageSize-->
        <!-- -->
        <span class="nop-pager-elevator">
            
            <input type="text" class="nop-pager-input" autocomplete="off" spellcheck="false" @keyup.enter="onInputChange"> 
        </span>
        <!--END -->
    </ul>
</template>

<script>
import Icon from '@/components/icon';
import Select, { Option } from '@/components/select';

const prefix = 'nop';

export default {
    name: 'nop-page',
    components: {
        Icon,
        Select,
        Option
    },
    props: {
        prefixCls: {
            type: String,
            default: prefix
        },
        current: {
            type: Number,
            default: 1
        },
        total: {
            type: Number,
            default: 0
        },
        pageSize: {
            type: Number,
            default: 10
        },
        pageSizeOpts: {
            type: Array,
            default() {
                return [15, 30, 50, 100];
            }
        },
        showTotal: {
            type: Boolean,
            default: true
        },
        showElevator: {
            type: Boolean,
            default: true
        },
        showSizer: {
            type: Boolean,
            default: true
        },
        styles: {
            type: Object
        }
    },
    data() {
        return {
            currentPage: this.current,
            currentPageSize: this.pageSize
        };
    },
    watch: {
        total(val) {
            let maxPage = Math.ceil(val / this.currentPageSize);
            if (maxPage < this.currentPage && maxPage > 0) {
                this.currentPage = maxPage;
            }
        },
        current(val) {
            // @NOTE  
            if (val <= this.allPages) {
                this.currentPage = val;
            }
        },
        pageSize(val) {
            this.currentPageSize = val;
        }
    },
    computed: {
        // 
        // 
        prevClass() {
            return [
                'nop-pager-item',
                'nop-pager-pre',
                {
                    ['nop-pager-item-disabled']: this.currentPage === 1
                }
            ];
        },
        // 
        nextClass() {
            return [
                'nop-pager-item',
                'nop-pager-next',
                {
                    ['nop-pager-item-disabled']:
                        this.currentPage === this.allPages
                }
            ];
        },
        // 
        firstPageClass() {
            return [
                'nop-pager-item',
                'nop-pager-first',
                {
                    ['nop-pager-item-active']: this.currentPage === 1
                }
            ];
        },
        // 
        lastPageClass() {
            return [
                'nop-pager-item',
                'nop-pager-last',
                {
                    ['nop-pager-item-active']:
                        this.currentPage === this.allPages
                }
            ];
        },
        // END
        allPages() {
            const allPage = Math.ceil(this.total / this.currentPageSize);
            return allPage === 0 ? 1 : allPage;
        }
    },
    methods: {
        //  
        // @NOTE  
        setPage(page) {
            this.currentPage = page;
        },
        changePage(page, from = '') {
            if (this.currentPage != page) {
                this.currentPage = page;
                // @NOTE .sync  prop 
                this.$emit('update:current', page);
                this.$emit('on-change', page);
            }
        },
        prev() {
            const current = this.currentPage;
            if (current <= 1) {
                return false;
            }
            this.changePage(current - 1);
        },
        next() {
            const current = this.currentPage;
            if (current >= this.allPages) {
                return false;
            }
            this.changePage(current + 1);
        },
        fastPrev() {
            const page = this.currentPage - 5;
            if (page > 0) {
                this.changePage(page);
            } else {
                this.changePage(1, 'size');
            }
        },
        fastNext() {
            const page = this.currentPage + 5;
            if (page > this.allPages) {
                this.changePage(this.allPages);
            } else {
                this.changePage(page);
            }
        },
        onSize(pageSize) {
            this.currentPageSize = pageSize;
            this.$emit('on-page-size-change', pageSize);
            // this.$nextTick(() => {
            this.changePage(1);
            // });
        },
        onPage(page) {
            this.changePage(page);
        },
        onInputChange(event) {
            let val = event.target.value.trim();
            let page = 0;

            if (/^[1-9][0-9]*$/.test(val + '')) {
                val = Number(val);
                if (val != this.currentPage) {
                    const allPages = this.allPages;

                    if (val > allPages) {
                        page = allPages;
                    } else {
                        page = val;
                    }
                }
            } else {
                page = 1;
            }

            if (page) {
                this.onPage(page);
                event.target.value = page;
            }
        },
        keyDown(e) {
            const key = e.keyCode;
            const condition =
                (key >= 48 && key <= 57) ||
                (key >= 96 && key <= 105) ||
                key === 8 ||
                key === 37 ||
                key === 39;

            if (!condition) {
                e.preventDefault();
            }
        },
        keyUp(e) {
            const key = e.keyCode;
            const val = parseInt(e.target.value);

            if (key === 38) {
                this.prev();
            } else if (key === 40) {
                this.next();
            } else if (key === 13) {
                let page = 1;

                if (val > this.allPages) {
                    page = this.allPages;
                } else if (val <= 0 || !val) {
                    page = 1;
                } else {
                    page = val;
                }

                e.target.value = page;
                this.changePage(page);
            }
        }
    }
};
</script>
